.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.mainUi {
  display: flex;
  justify-content: flex-start;
  width: 25%;
  align-items: center;
  width: fit-content;
  margin-left: auto;
  margin-right: 10px;
}

.childSlider {
  color: #cccbcc;
  display: none;
  transition: display 5s;
}

.mainUi:hover {
  width: 200px;
}

.mainUi:hover .childSlider {
  display: block;
}
